.App {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  .a4 {
    width: 70vw;
    max-width: 594px;
    height: 49.5vw;
    max-height: 420px;
    box-shadow: 0 11px 15px -7px rgba(0, 0, 0, 0.2),
      0 24px 38px 3px rgba(0, 0, 0, 0.14), 0 9px 46px 8px rgba(0, 0, 0, 0.12) !important;
    perspective: 800px;
    transition: all 0.3s ease;
    display: flex;
    align-items: center;
    justify-content: space-between;
    img {
      width: 72px;
      height: 72px;
    }
  }
}
body {
  margin: 0;
  /* overflow: hidden; */
}
*::-webkit-scrollbar {
  display: none; /* Chrome Safari */
}
